html {
  font-size: 62.5%; }

body {
  font-size: 1.6rem;
  font-family: "Noto Sans JP", sans-serif;
  background-color: #eef3f9; }

p {
  text-align: justify; }

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.right-bottom-border {
  position: relative;
  border-bottom: 12px solid #eef3f9;
  border-right: 12px solid #eef3f9;
  border-bottom-right-radius: 3rem; }
  .right-bottom-border::after {
    content: "";
    position: absolute;
    bottom: -12px;
    right: -12px;
    border: 15px solid #eef3f9;
    border-color: transparent #eef3f9 #eef3f9 transparent; }

.right-top-border {
  position: relative;
  border-top: 12px solid #eef3f9;
  border-right: 12px solid #eef3f9;
  border-top-right-radius: 3rem; }
  .right-top-border::after {
    content: "";
    position: absolute;
    top: -12px;
    right: -12px;
    border: 15px solid #eef3f9;
    border-color: #eef3f9 #eef3f9 transparent transparent; }

.right-border {
  border-right: 12px solid #eef3f9; }

body {
  display: grid;
  grid-template-columns: 2fr 10fr;
  height: 100vh; }

.sidebar {
  display: grid;
  background: linear-gradient(0deg, #259dff 0%, #3a39ff 100%);
  grid-template-rows: repeat(3, minmax(min-content, max-content)) 1fr; }
  .sidebar .logo-container {
    display: flex;
    width: 100%;
    justify-content: center;
    padding-top: 4.8rem;
    padding-bottom: 9.6rem; }
    .sidebar .logo-container .logo {
      font-size: 6.4rem;
      color: #FFF; }
  .sidebar .sidebar-item-container {
    display: flex;
    width: 100%; }
    .sidebar .sidebar-item-container .sidebar-item {
      display: flex;
      align-items: center;
      padding: 1.6rem 2.4rem;
      color: #cccccc;
      width: 100%;
      font-size: 1.4rem;
      font-weight: 100;
      text-align: start;
      z-index: 1; }
      .sidebar .sidebar-item-container .sidebar-item:hover {
        color: #FFF;
        font-weight: 400; }
    .sidebar .sidebar-item-container .sidebar-item-icon {
      margin-right: 1.6rem; }
      .sidebar .sidebar-item-container .sidebar-item-icon.fa-edit {
        margin-right: 1.075rem; }
  .sidebar .sidebar-item-container.selected {
    background-color: #eef3f9;
    border-right: 12px solid #eef3f9;
    margin: -12px 0; }
    .sidebar .sidebar-item-container.selected .sidebar-item {
      color: #24292e;
      font-weight: 700; }
    .sidebar .sidebar-item-container.selected .sidebar-item-icon {
      color: blue; }

.upload-section-selected {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }

.upload-section {
  border: 3px dashed #676f77;
  border-radius: 3rem;
  height: 90%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #FFF;
  transition: 0.5s; }
  .upload-section .upload-icon {
    font-size: 8rem;
    color: #259dff;
    text-align: center;
    margin-bottom: 8rem;
    transition: 0.5s; }
  .upload-section h2 {
    text-align: center; }
  .upload-section.highlight {
    border: 3px solid #259dff; }
    .upload-section.highlight .upload-icon {
      color: #0085f1; }

.form input[type="submit"] {
  display: none; }

.upload-input-container {
  position: relative; }
  .upload-input-container input {
    width: 0px;
    height: 0px;
    position: absolute;
    opacity: 0.01s; }
  .upload-input-container .flex-upload-container {
    display: flex;
    justify-content: center; }
  .upload-input-container label {
    margin-top: 1.6rem;
    padding: 0.7rem 1.6rem;
    background-color: #259dff;
    color: #FFF;
    font-weight: 100;
    border-radius: 1rem;
    cursor: pointer; }
    .upload-input-container label:hover {
      background-color: #0085f1; }

.loading-container {
  position: absolute;
  width: 0;
  opacity: 0; }
  .loading-container.show {
    position: fixed;
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 2; }
    .loading-container.show path {
      fill: url("#myGradient");
      animation: loadPage 1s forwards; }
    .loading-container.show .loading-animation {
      position: absolute;
      opacity: 0;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      width: 9rem;
      height: 9rem;
      background-color: #b3b3b3;
      border-radius: 1rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      animation: startLoadingAnimation 0.5s 0.5s forwards, middleLoadingAnimation 1s 1s infinite forwards; }
      .loading-container.show .loading-animation.show-success-message {
        animation: showSuccessMessage 1s forwards; }
      .loading-container.show .loading-animation.show-error-message {
        animation: showErrorMessage 1s forwards; }
    .loading-container.show .loading-text {
      opacity: 0;
      display: inline-block;
      position: relative; }
      .loading-container.show .loading-text.show-text-message {
        animation: showTextMessage 1s forwards; }
  .loading-container.hide {
    animation: removeContainer 0s 1s; }
    .loading-container.hide .loading-animation {
      animation: middleLoadingAnimation 0.5s infinite, hideLoadingAnimation 0.5s forwards; }
    .loading-container.hide path {
      animation: removeLoadPage 1s forwards; }

@keyframes loadPage {
  to {
    d: path("M 0,0 c 0,0 63.5,-16.5 80,0 16.5,16.5 0,60 0,60 L 0,60 Z"); } }

@keyframes removeLoadPage {
  from {
    d: path("M 0,0 c 0,0 63.5,-16.5 80,0 16.5,16.5 0,60 0,60 L 0,60 Z"); }
  to {
    d: path("M 0,0 c 0,0 -16.5,43.5 0,60 16.5,16.5 80,0 80,0 L 0,60 Z"); } }

@keyframes hideLoadingAnimation {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes removeContainer {
  100% {
    position: absolute;
    width: 0;
    opacity: 0; } }

@keyframes startLoadingAnimation {
  0% {
    opacity: 0; }
  100% {
    opacity: 1;
    transform: rotateX(180deg); } }

@keyframes middleLoadingAnimation {
  0% {
    opacity: 1; }
  50% {
    opacity: 1;
    transform: rotateZ(180deg); }
  100% {
    opacity: 1;
    transform: rotateY(180deg); } }

@keyframes showSuccessMessage {
  0% {
    opacity: 1; }
  100% {
    opacity: 1;
    width: 20rem;
    background-color: #56FF19; } }

@keyframes showErrorMessage {
  0% {
    opacity: 1; }
  100% {
    opacity: 1;
    width: 20rem;
    background-color: #FF4740; } }

@keyframes showTextMessage {
  0% {
    opacity: 0;
    left: 3rem; }
  100% {
    opacity: 1;
    left: 0; } }

@keyframes popUpIn {
  0% {
    opacity: 0;
    margin-top: -90%; }
  75% {
    margin-top: 10%; }
  100% {
    opacity: 1; } }

@keyframes popUpOut {
  0% {
    opacity: 1; }
  75% {
    opacity: 1;
    margin-top: -50%; }
  100% {
    opacity: 0;
    margin-top: 30%; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOut {
  0% {
    background-color: rgba(85, 85, 85, 0.8); }
  100% {
    background-color: rgba(85, 85, 85, 0); } }

@keyframes popInCross {
  0% {
    opacity: 0;
    width: 0;
    height: 0; }
  50% {
    opacity: 0.2;
    width: 0;
    height: 0; }
  100% {
    opacity: 1;
    width: 0.5rem;
    height: 5rem; } }

@keyframes popInCrossCircle {
  0% {
    opacity: 0;
    height: 0;
    width: 0; }
  50% {
    opacity: 0.7;
    height: 3rem;
    width: 9rem; }
  100% {
    opacity: 1;
    height: 9rem;
    width: 9rem; } }

body.alert {
  overflow: hidden; }

.alert-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(85, 85, 85, 0.8);
  display: flex;
  justify-content: center;
  align-items: center; }
  .alert-container.in {
    animation: fadeIn 0.3s; }
  .alert-container.out {
    animation: fadeOut 1s; }
  .alert-container .alert {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #FFF;
    border-radius: 1rem;
    padding: 2.4rem 1.6rem 1.6rem 1.6rem; }
    .alert-container .alert.in {
      animation: popUpIn 0.3s; }
    .alert-container .alert.out {
      animation: popUpOut 0.5s;
      opacity: 0; }
    .alert-container .alert span {
      position: relative;
      width: 9rem;
      height: 9rem;
      border-radius: 50%;
      border: 5px solid #9C3927;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 3.2rem;
      animation: popInCrossCircle 0.3s; }
      .alert-container .alert span::before {
        content: "";
        width: 0.5rem;
        height: 5rem;
        background-color: #9C3927;
        transform: rotate(45deg);
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        border-radius: 1rem;
        animation: popInCross 0.5s; }
      .alert-container .alert span::after {
        content: "";
        width: 0.5rem;
        height: 5rem;
        background-color: #9C3927;
        transform: rotate(-45deg);
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        border-radius: 1rem;
        animation: popInCross 0.5s; }
    .alert-container .alert .alert-title {
      font-size: 3.2rem;
      font-weight: 700;
      color: #3a434b;
      margin-bottom: 1.6rem; }
    .alert-container .alert .alert-info {
      color: #3a434b;
      font-size: 1.76rem;
      margin-bottom: 3.2rem; }
    .alert-container .alert .button {
      background-color: #259dff;
      width: 20%;
      height: 5rem;
      text-transform: uppercase;
      color: #FFF;
      border-radius: 1rem; }
      .alert-container .alert .button:hover {
        background-color: #0085f1; }
    .alert-container .alert p {
      margin: 0; }
